import { observer } from "mobx-react-lite";
import React, { useContext, useState } from "react";
import { Container, Image, Nav, Navbar, NavDropdown, Offcanvas } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { CART_ROUTE, CONTACTS_ROUTE, DISK_LIST_ROUTE, HOME_ROUTE, SERVICE_AVTOMOJKA_ROUTE, SERVICE_HRANENIESHIN_ROUTE, SERVICE_SHINOMONTAZH_ROUTE, TIRES_LIST_ROUTE, SERVICE_STO_ROUTE, CATALOG_LIST_ROUTE } from "../../utils/consts";
import { declOfNum } from "../../utils/functions";
import cartImg from '../../assets/img/basket_black.svg'
import cartFullImg from '../../assets/img/basket_red.svg'
import logo from '../../assets/img/logo.svg'
import { Context } from "../..";
import './style.css'
const NavBar = observer(() => {
    const navigate = useNavigate()
    const { cart, tires } = useContext(Context)
    const [expanded, setExpanded] = useState(false);

    return (
        <>




            <Navbar className="d-none d-lg-flex  shadow-sm" style={{ zIndex: 99999 }} collapseOnSelect expand="lg" bg="light" variant="light">
                <Container >
                    <Navbar.Brand  onClick={() => navigate(HOME_ROUTE)}><Image src={logo} alt="logo" /></Navbar.Brand>
                    <Navbar.Collapse id="responsive-navbar-nav m-auto">

                        <Nav className="m-auto">
                            <Nav.Link onClick={() => navigate(CONTACTS_ROUTE)}>о компании</Nav.Link>
                            <Nav.Link onClick={() => navigate(CONTACTS_ROUTE)}>доставка и оплата</Nav.Link>
                            <Nav.Link onClick={() => navigate(CONTACTS_ROUTE)}>контакты</Nav.Link>


                        </Nav>

                    </Navbar.Collapse>
                    <div style={{ marginLeft: 'auto', }}><a style={{ textDecoration: 'none', fontWeight: '500', color: '#E20613' }} href='tel:+73456275050'>+7 (3456) 27-50-50</a></div>


                </Container>
            </Navbar>

            <Navbar bg="light" variant="light" className="d-none d-lg-flex sticky-top shadow-sm" >
                <Container>
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="w-100 d-flex  ">

                            <button type="button" className="btn btn-outline-danger btn-small me-2" onClick={() => { tires.setFilter(Object.assign(tires.filter, { 'type_list': '' })); navigate(TIRES_LIST_ROUTE) }}>Шины</button>
                            <button type="button" className="btn btn-outline-danger btn-small me-2" onClick={() => navigate(DISK_LIST_ROUTE)}>Диски</button>
                            <button type="button" className="btn btn-outline-danger btn-small me-2" onClick={() => { tires.setFilter(Object.assign(tires.filter, { 'type_list': 'truck' })); navigate(TIRES_LIST_ROUTE) }}>Грузовые шины</button>
                            <button type="button" className="btn btn-outline-danger btn-small me-2" onClick={() => navigate(CATALOG_LIST_ROUTE) }>Автозапчасти</button>
                            <NavDropdown className="p-0" title={<button type="button" className="btn btn-outline-danger btn-small me-2">Услуги</button>} id="basic-nav-dropdown">
                                <NavDropdown.Item href={SERVICE_STO_ROUTE}>СТО</NavDropdown.Item>
                                <NavDropdown.Item href={SERVICE_SHINOMONTAZH_ROUTE}>Шиномонтаж</NavDropdown.Item>
                                <NavDropdown.Item href={SERVICE_AVTOMOJKA_ROUTE}>Автомойка</NavDropdown.Item>
                                <NavDropdown.Item href={SERVICE_HRANENIESHIN_ROUTE}>Хранение шин</NavDropdown.Item>
                            </NavDropdown>

                        </Nav>
                        <div className="d-flex">

                            <div style={{ display: 'flex', alignItems: 'center', cursor: 'pointer' }} onClick={() => navigate(CART_ROUTE)}>
                                <img
                                    alt=""
                                    width="24px"
                                    height='24px'
                                    src={cart.count > 0 ? cartFullImg : cartImg}
                                    className="d-inline-block align-top"
                                />{' '}
                                <div className="d-flex flex-column h-100 justify-content-around ms-1" >
                                    <div style={{ fontSize: '12px', lineHeight: '17px', textTransform: 'uppercase' }}>Корзина</div>
                                    <div className={cart.count > 0 ? 'menu-basket-text color-pink' : 'menu-basket-text'}  >{cart.count > 0 ? cart.count + declOfNum(cart.count, [' Товар', ' Товара', ' Товаров']) : 'Нет товаров'} </div>
                                </div>
                            </div>
                        </div>


                    </Navbar.Collapse>
                </Container>
            </Navbar>
            {
                // Мобилка
            }
            <Navbar className="d-flex d-lg-none" variant="light" bg="light" expanded={expanded} expand={false} collapseOnSelect>
                <Container fluid>
                    <Navbar.Toggle aria-controls="offcanvasNavbar" onClick={() => setExpanded(expanded ? false : "expanded")} />


                    <Navbar.Offcanvas
                        id="offcanvasNavbar"
                        aria-labelledby="offcanvasNavbarLabel"
                        placement="start"
                        variant="light"
                    >
                        <Offcanvas.Header >
                            <Offcanvas.Title id="offcanvasNavbarLabel" onClick={() => { setExpanded(expanded ? false : "expanded"); navigate('/') }}><Image src={logo} alt="logo" /></Offcanvas.Title>
                            <button type="button" className="btn-close" aria-label="Close" onClick={() => setExpanded(expanded ? false : "expanded")}></button>
                        </Offcanvas.Header>
                        <Offcanvas.Body>
                            <Nav className="justify-content-end flex-grow-1 pe-3">
                                <Nav.Link onClick={() => { setExpanded(expanded ? false : "expanded"); tires.setFilter(Object.assign(tires.filter, { 'type_list': '' })); navigate(TIRES_LIST_ROUTE) }}>Шины</Nav.Link>
                                <Nav.Link onClick={() => { setExpanded(expanded ? false : "expanded"); navigate(DISK_LIST_ROUTE) }}>Диски</Nav.Link>
                                <Nav.Link onClick={() => { setExpanded(expanded ? false : "expanded"); tires.setFilter(Object.assign(tires.filter, { 'type_list': 'truck' })); navigate(TIRES_LIST_ROUTE) }}>Грузовые шины</Nav.Link>
                                <Nav.Link onClick={() => { setExpanded(expanded ? false : "expanded"); navigate(CATALOG_LIST_ROUTE)  }}>Автозапчасти</Nav.Link>
                                <NavDropdown title="Услуги" id="basic-nav-dropdown">
                                    <NavDropdown.Item href={SERVICE_STO_ROUTE}>СТО</NavDropdown.Item>
                                    <NavDropdown.Item href={SERVICE_SHINOMONTAZH_ROUTE}>Шиномонтаж</NavDropdown.Item>
                                    <NavDropdown.Item href={SERVICE_AVTOMOJKA_ROUTE}>Автомойка</NavDropdown.Item>
                                    <NavDropdown.Item href={SERVICE_HRANENIESHIN_ROUTE}>Хранение шин</NavDropdown.Item>
                                </NavDropdown>

                            </Nav>
                            <div className="mt-4"><a href="tel:+73456275050" style={{ textDecoration: 'none', fontWeight: '500', color: '#333' }}>+7 (3456) 27-50-50</a></div>
                        </Offcanvas.Body>
                    </Navbar.Offcanvas>



                    <div className="d-flex">

                        <div style={{ display: 'flex', alignItems: 'center', cursor: 'pointer', marginRight: '1rem' }} onClick={() => navigate(CART_ROUTE)}>
                            <img
                                alt=""
                                width="24px"
                                height='24px'
                                src={cart.count > 0 ? cartFullImg : cartImg}
                                className="d-inline-block align-top"
                            />{' '}
                            <div className="d-flex flex-column" style={{ marginLeft: '.25rem' }} >
                                <div style={{ fontSize: '12px', lineHeight: '17px', textTransform: 'uppercase' }}>Корзина</div>
                                <div className={cart.count > 0 ? 'menu-basket-text color-pink' : 'menu-basket-text'}  >{cart.count > 0 ? cart.count + declOfNum(cart.count, [' Товар', ' Товара', ' Товаров']) : 'Нет товаров'} </div>
                            </div>
                        </div>
                    </div>
                </Container>
            </Navbar>
        </>

    );

})


export default NavBar;