import { observer } from "mobx-react-lite"
import React, { useContext, useState, useEffect } from "react";
import { Context } from "../";
import { Col, Container, Form, Pagination, Row } from "react-bootstrap"
import CatalogListItem from "../components/CatalogListItem"
import { fetchDisk, fetchTires } from "../http/catalog"
import { dataDisk, dataTires, dataWrh } from "../utils/data";
import { declOfNum } from "../utils/functions";


const DiskListPage = observer(() => {

    const { disk } = useContext(Context)

    const [isFetched, setIsFetched] = useState(false)
    const pageCount = Math.ceil(disk.totalCount / disk.limit)
    const pages = []
    
    for (let i = 0; i < pageCount; i++) {
        if (disk.page + 3 > i && disk.page - 3 < i)
            pages.push(i + 1)
    }

    const setPage = page => {
        window.scrollTo({ top: 0 });
        setIsFetched(true)
        disk.setItems([])
        fetchDisk({ page: page, limit: disk.limit, filter: disk.filter }).then(data => {
            
            if (typeof data.GetFindDiskResult.price_rest_list.DiskPriceRest !== 'undefined') {
                if( Array.isArray(data.GetFindDiskResult.price_rest_list.DiskPriceRest))
                    disk.setItems(data.GetFindDiskResult.price_rest_list.DiskPriceRest)
                else
                    disk.setItems([data.GetFindDiskResult.price_rest_list.DiskPriceRest])
                disk.setLogistics(data.GetFindDiskResult.warehouseLogistics.WarehouseLogistic)
                disk.setTotalCount(data.GetFindDiskResult.totalPages * disk.limit)
            }
            setIsFetched(false)
        })
        disk.setPage(page)
    }


    useEffect(() => {
        window.scrollTo({ top: 0 });
        disk.setItems([])
        setIsFetched(true)
        
        fetchDisk({ page: disk.page, limit: disk.limit, filter: disk.filter }).then(data => {
            
            if (typeof data.GetFindDiskResult.price_rest_list.DiskPriceRest !== 'undefined') {
                if( Array.isArray(data.GetFindDiskResult.price_rest_list.DiskPriceRest))
                    disk.setItems(data.GetFindDiskResult.price_rest_list.DiskPriceRest)
                else
                    disk.setItems([data.GetFindDiskResult.price_rest_list.DiskPriceRest])
                disk.setLogistics(data.GetFindDiskResult.warehouseLogistics.WarehouseLogistic)
                disk.setTotalCount(data.GetFindDiskResult.totalPages * disk.limit)
            }
            setIsFetched(false)
        })
    }, [disk.page, disk.filter.type_list])


    const handleSelectFilterChanged = (e) => {
        let temp = {}
        temp[e.target.attributes['filter-name'].value] = e.target.value
        disk.setFilter(Object.assign(disk.filter, temp))
        setPage(1)
    }

    return (
        <div>


            <Container className="mt-4 TiresListPage ">
                <Row>
                    <Col lg={2} >
                        <div class="">
                            <Row>
                        <div className="shadow-sm bg-white pe-lg-1">


                            <div className="px-2 py-2 font-weight-bold">
                            



                            
                            <span>Наличие</span>
                            <Form.Select className="mb-3" haria-label="Наличие" filter-name="wrh" onChange={handleSelectFilterChanged} value={disk.filter.wrh}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataWrh.map(el => {
                                    return <option value={el.whId}>Под заказ {el.logistDays + 1} {el.logistDays + 1 > 0 ? declOfNum(el.logistDays + 1, [' день', ' дня', ' дней']) : 'Уточняйте'}</option>
                                })}
                         
                            </Form.Select>


                            <span>Производитель</span>
                            <Form.Select className="mb-3" haria-label="Производитель" filter-name="brand_list" onChange={handleSelectFilterChanged} value={disk.filter.brand_list}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataDisk.brand_list.map(el => {
                                    return <option value={el.text}>{el.text}</option>
                                })}
                         
                            </Form.Select>

                            <span>Тип диска</span>
                            <Form.Select className="mb-3" haria-label="Тип диска" filter-name="type_list"  onChange={handleSelectFilterChanged} value={disk.filter.type_list}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataDisk.type_list.map(el => {
                                    return <option value={el.val}>{el.text}</option>
                                })}
                         
                            </Form.Select>

                            <span>Ширина</span>
                            <Form.Select className="mb-3" haria-label="Ширина" filter-name="width"  onChange={handleSelectFilterChanged} value={disk.filter.width}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataDisk.width.map(el => {
                                    return <option value={el.text}>{el.text}</option>
                                })}
                         
                            </Form.Select>



                            <span>Диаметр</span>
                            <Form.Select className="mb-3" haria-label="Диаметр" filter-name="diameter"  onChange={handleSelectFilterChanged} value={disk.filter.diameter}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataDisk.diameter.map(el => {
                                    return <option value={el.text}>{el.text}</option>
                                })}
                         
                            </Form.Select>

                            <span>Количество отверстий</span>
                            <Form.Select className="mb-3" haria-label="Количество отверстий" filter-name="bolts_count"  onChange={handleSelectFilterChanged} value={disk.filter.bolts_count}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataDisk.bolts_count.map(el => {
                                    return <option value={el.val}>{el.text}</option>
                                })}
                         
                            </Form.Select>

                            <span>Диаметр крепежа</span>
                            <Form.Select className="mb-3" haria-label="Диаметр крепежа" filter-name="bolts_spacing"  onChange={handleSelectFilterChanged} value={disk.filter.bolts_spacing}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataDisk.bolts_spacing.map(el => {
                                    return <option value={el.val}>{el.text}</option>
                                })}
                         
                            </Form.Select>

                            <span>Вылет</span>
                            <Form.Select className="mb-3" haria-label="Вылет" filter-name="et"  onChange={handleSelectFilterChanged} value={disk.filter.et}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataDisk.et.map(el => {
                                    return <option value={el.val}>{el.text}</option>
                                })}
                         
                            </Form.Select>

                            <span>Диаметр ступичного отверстия</span>
                            <Form.Select className="mb-3" haria-label="Диаметр ступичного отверстия" filter-name="dia"  onChange={handleSelectFilterChanged} value={disk.filter.dia}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataDisk.dia.map(el => {
                                    return <option value={el.val}>{el.text}</option>
                                })}
                         
                            </Form.Select>


                            
                            </div>

                            
                        </div>
                        </Row>
                        </div>
                    </Col>
                    <Col lg={10} >
                        <div className="">
                            <Row>
                                {
                                    (!isFetched && disk.count === 0) ? 
                                        
                                            <>
                                                Ничего не нашлось
                                            </>
                                        : 
                                    
                                    (disk.count === 0 && isFetched) ?
                                        [...Array(20)].map((x, i) => {
                                            return <CatalogListItem key={i} loading />
                                        }
                                        )
                                        :
                                        disk.items.map(el => {
                                            return <CatalogListItem key={el.code} data={el} logistic={disk.logistics} />
                                        })
                                }





                            </Row>
                        </div>
                    </Col>
                </Row>
            </Container>


            {(pages.length > 1 && disk.count > 0) ? 
            <div className="w-100 d-flex justify-content-center" style={{ marginBottom: '60px', marginTop: '50px' }}>
                <Pagination className="mt-3" >
                    {pages.map(page =>
                        <Pagination.Item
                            key={page}
                            active={disk.page === page}
                            onClick={() => setPage(page)}
                        >
                            {page}
                        </Pagination.Item>
                    )}
                </Pagination>
            </div>
            : ''}

        </div>
    )
})
export default DiskListPage