import { makeAutoObservable } from "mobx"

export default class TiresStore{
    constructor(){
        this._limit = 20
        this._items = []
        this._totalCount = 0
        this._page = 1
        this._logistics = []
        this._filter = {
            'sort': 3,
            'season': '',
            'brand_list' : '',
            'width': '',
            'height': '',
            'diameter': '',
            'thorn': '',
            'type_list': '',
            'wrh': ''
        }

        makeAutoObservable(this)
    }

    setFilter(filter){
        /*products.sort(function(a, b) {
            return a.price - b.price;
          })*/
        this._filter = filter
    }

    setLogistics(logistics){
        /*products.sort(function(a, b) {
            return a.price - b.price;
          })*/
        this._logistics = logistics
    }

    setItems(products){
        /*products.sort(function(a, b) {
            return a.price - b.price;
          })*/
        this._items = products
    }
 
    getItem(code){
        let tws = null
        this._items.map(element => {
            if(element.code == code)
                tws = element
        });
        return tws
    }
    setPage(page) {
        this._page = page
    }
    setTotalCount(c) {
        this._totalCount = c
    }

    get logistics(){
        return this._logistics
    }
    get items(){
        return this._items 
    }
    get count(){
        return this._items.length
    }
    get totalCount(){
        return this._totalCount
    }
    get limit(){
        return this._limit
    }
    get page() {
        return this._page
    }
    get filter() {
        return this._filter
    }
    
}