import { observer } from "mobx-react-lite"
import { useContext, useEffect, useState } from "react"
import { Col, Container, Image, Row } from "react-bootstrap"
import { useParams } from "react-router-dom"
import { Context } from ".."
import { fetchOneTire, fetchTires } from "../http/catalog"
import { declOfNum } from "../utils/functions"
import { lang } from "../utils/lang"









const TiresDetailPage = observer(() => {
    const { cart } = useContext(Context)
    const disabledProps = [
        'img_small',
        'img_big_pish',
        'img_big_my',
        'name',
    ]

    const params = useParams()

    const [inputValue, setInputValue] = useState(1)
    const [item, setItem] = useState(null)
    const [logistic, setLogistic] = useState([])
    const [price, setPrice] = useState(0)
    const [wrh, setWrh] = useState(0)
    const [rest, setRest] = useState(0)

    const slug = params.slug

    useEffect(() => {

        window.scrollTo({ top: 0 });
        setItem(null)
        fetchOneTire({ code: slug }).then(data => {
            console.log(data.GetGoodsInfoResult.tyreList.TyreContainer)
            setItem(data.GetGoodsInfoResult.tyreList.TyreContainer)
            //setItem(data)
        })
        fetchTires({ filter: { code: slug } }).then(data => {
            console.log(data)
            setLogistic(data.GetFindTyreResult.warehouseLogistics.WarehouseLogistic)
            setPrice(data.GetFindTyreResult.price_rest_list.TyrePriceRest.whpr.wh_price_rest.price_rozn)
            setWrh(data.GetFindTyreResult.price_rest_list.TyrePriceRest.whpr.wh_price_rest.wrh)
            setRest(data.GetFindTyreResult.price_rest_list.TyrePriceRest.whpr.wh_price_rest.rest)
            //setLogistic(data)
            //setItem(data.GetGoodsInfoResult.tyreList.TyreContainer)
            //setItem(data)
        })
    }, [slug])


    var itemParams = [];
    for (let key in item) {
        if (item[key] !== "" && item[key] !== null && disabledProps.indexOf(key) === -1) {
            let value = ""
            if (typeof item[key] === 'string') value = item[key]
            if (typeof item[key] === 'boolean') value = item[key] ? "Да" : "Нет"
            if (key === 'season') value = lang.list.season[item[key]]
            if (key === 'type') value = lang.list.type_list_tires[item[key]]
            itemParams.push({ name: lang[key], val: value });
        }
    }
    if (item === null)
        return (<></>)


    let logistDays = 0


    logistic.forEach(el => {
        if (el.whId === wrh)
            logistDays = el.logistDays
    });

    const handlePlus = () => inputValue >= rest ? rest : setInputValue(inputValue + 1)
    const handleMinus = () => inputValue <= 1 ? 1 : setInputValue(inputValue - 1)
    return (
        <Container className="product-detail mt-5">
            <Row>
                <Col lg={5}>
                    <Image src={item.img_big_my} width="100%"/>
                </Col>
                <Col lg={7}>
                    <h2>{item.name}</h2>

                    <div className="">Доставим: под заказ ~ {logistDays + 1} {logistDays + 1 > 0 ? declOfNum(logistDays + 1, [' день', ' дня', ' дней']) : 'Уточняйте'}</div>
                    <div className="">Остаток:&nbsp;&nbsp;&nbsp; {rest > 4 ? '> 4' : rest}  шт.</div>
                    <div className="w-100 CatalogListItem-item-price d-flex   py-3">
                        <div className="input-group w-auto mb-3 ms-0 me-3">
                            <div className="input-group-prepend">
                                <button onClick={handleMinus} className="btn btn-secondary btn-sm h-100" style={{ borderTopRightRadius: 0, borderBottomRightRadius: 0 }} id="minus-btn"><i className="fa fa-minus"></i></button>
                            </div>
                            <input type="number" id="qty_input" className="form-control form-control-sm text-center" value={inputValue} min="1" max={rest} style={{ maxWidth: '50px' }} disabled />
                            <div className="input-group-prepend">
                                <button onClick={handlePlus} className="btn btn-secondary btn-sm h-100" style={{ borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }} id="plus-btn"><i className="fa fa-plus"></i></button>
                            </div>
                        </div>
                        <div className="input-group w-auto mb-3">
                            <button  onClick={() => cart.addItem(slug, inputValue)} className={`product-detail btn ${cart.checkItem(slug) === false ? 'btn-danger' : 'btn-success'} `}>{cart.checkItem(slug) === false ? 'В корзину' : 'В корзине'}</button>
                        </div>
                    </div>
                    <caption class="product-params__caption">Характеристики</caption>
                    <table class="product-params__table">
                        
                        <tbody >
                            {itemParams.map(el => {
                                return (
                                    <tr key={el.val + el.name} class="product-params__row">
                                        <th class="product-params__cell">
                                            <span class="product-params__cell-decor">
                                                <span>{el.name}</span>
                                            </span>
                                        </th>
                                        <td class="product-params__cell">{el.val}</td>
                                    </tr>)
                            })}
                            
                        </tbody>
                    </table>
                    
                </Col>

            </Row>



        </Container>
    )
})
export default TiresDetailPage