import { observer } from "mobx-react-lite"
import { useContext } from "react"
import { Button, Col, Container, Form, Row, Tab, Tabs } from "react-bootstrap"
import { Link, useNavigate } from "react-router-dom"
import { Context } from ".."
import { DISK_LIST_ROUTE, TIRES_LIST_ROUTE } from "../utils/consts"
import { dataTires, dataDisk } from "../utils/data"


const HomePage = observer(() => {
    const { tires, disk } = useContext(Context)
    const navigate = useNavigate()
    const handleSelectFilterChanged = (e) => {
        let temp = {}
        temp[e.target.attributes['filter-name'].value] = e.target.value
        tires.setFilter(Object.assign(tires.filter, temp))

    }
    const handleDiskSelectFilterChanged = (e) => {
        let temp = {}
        temp[e.target.attributes['filter-name'].value] = e.target.value
        disk.setFilter(Object.assign(tires.filter, temp))

    }
    return (
        <div className="w-100">

            <div className="bg-white py-md-5 ">
                <Container className="HomePage-banner">
                    <div>
                        <h1>ШИНЫ, ДИСКИ <br />АВТОЗАПЧАСТИ</h1>
                        <span>также комплексный ремонт и техническое обслуживание <br />легковых и легкогрузовых автомобилей любых марок</span>

                        <div className=" pt-5">
                            <Row className="w-lg-100">
                                <Col lg={3} className="1m-1  pb-2" ><Button className="w-100" variant="danger" size-md='lg'>СТО</Button></Col>
                                <Col lg={4} className="1m-1 ps-lg-0 pb-2" ><Button className="w-100 " variant="danger" size-md='lg'>Автомойка</Button>  </Col>
                                <Col lg={4} className="1m-1 ps-lg-0" ><Button className="w-100" variant="danger" size-md='lg'>Шиномонтаж</Button> </Col>
                            </Row>

                        </div>
                    </div>

                </Container>
            </div>

            <Container className="">
                <Tabs defaultActiveKey="tires" className=" HomePage-tabs " style={{ marginTop: '40px', marginBottom: '0!Important', }}>
                    <Tab eventKey="tires" title="Шины" className=" p-4 shadow-sm2" style={{ background: '#fcfcfc' }}>
                        <Row>
                            <Col lg={4}>
                                <span>Сезон</span>
                                <Form.Select className="mb-3" aria-label="Сезон" filter-name="season" onChange={handleSelectFilterChanged} value={tires.filter.season}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataTires.season.map(el => {
                                        return <option value={el.val}>{el.text}</option>
                                    })}

                                </Form.Select>
                                <span>Производитель</span>
                                <Form.Select className="mb-3" haria-label="Производитель" filter-name="brand_list" onChange={handleSelectFilterChanged} value={tires.filter.brand_list}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataTires.brand_list.map(el => {
                                        return <option value={el.text}>{el.text}</option>
                                    })}

                                </Form.Select>




                            </Col>
                            <Col lg={4}>

                                <span>Ширина</span>
                                <Form.Select className="mb-3" haria-label="Ширина" filter-name="width" onChange={handleSelectFilterChanged} value={tires.filter.width}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataTires.width.map(el => {
                                        return <option value={el.text}>{el.text}</option>
                                    })}

                                </Form.Select>
                                <span>Высота</span>
                                <Form.Select className="mb-3" haria-label="Высота" filter-name="height" onChange={handleSelectFilterChanged} value={tires.filter.height}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataTires.height.map(el => {
                                        return <option value={el.text}>{el.text}</option>
                                    })}

                                </Form.Select>
                            </Col>
                            <Col lg={4}>


                                <span>Диаметр</span>
                                <Form.Select className="mb-3" haria-label="Диаметр" filter-name="diameter" onChange={handleSelectFilterChanged} value={tires.filter.diameter}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataTires.diameter.map(el => {
                                        return <option value={el.text}>{el.text}</option>
                                    })}

                                </Form.Select>
                            </Col>
                        </Row>

                        <Row>
                            <Col>
                                <Button variant="danger" size-md='lg' onClick={() => navigate(TIRES_LIST_ROUTE)}>Показать</Button>
                            </Col>
                        </Row>
                    </Tab>
                    <Tab eventKey="disk" title="Диски" className=" p-4 shadow-sm2" style={{ background: '#fcfcfc;' }}>
                        <Row>
                            <Col lg={4}>
                                <span>Производитель</span>
                                <Form.Select className="mb-3" haria-label="Производитель" filter-name="brand_list" onChange={handleDiskSelectFilterChanged} value={disk.filter.brand_list}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataDisk.brand_list.map(el => {
                                        return <option value={el.text}>{el.text}</option>
                                    })}

                                </Form.Select>

                                <span>Тип диска</span>
                                <Form.Select className="mb-3" haria-label="Тип диска" filter-name="type_list" onChange={handleDiskSelectFilterChanged} value={disk.filter.type_list}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataDisk.type_list.map(el => {
                                        return <option value={el.val}>{el.text}</option>
                                    })}

                                </Form.Select>

                                <span>Ширина</span>
                                <Form.Select className="mb-3" haria-label="Ширина" filter-name="width" onChange={handleDiskSelectFilterChanged} value={disk.filter.width}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataDisk.width.map(el => {
                                        return <option value={el.text}>{el.text}</option>
                                    })}

                                </Form.Select>









                            </Col>
                            <Col lg={4}>
                                <span>Диаметр</span>
                                <Form.Select className="mb-3" haria-label="Диаметр" filter-name="diameter" onChange={handleDiskSelectFilterChanged} value={disk.filter.diameter}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataDisk.diameter.map(el => {
                                        return <option value={el.text}>{el.text}</option>
                                    })}

                                </Form.Select>

                                <span>Количество отверстий</span>
                                <Form.Select className="mb-3" haria-label="Количество отверстий" filter-name="bolts_count" onChange={handleDiskSelectFilterChanged} value={disk.filter.bolts_count}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataDisk.bolts_count.map(el => {
                                        return <option value={el.val}>{el.text}</option>
                                    })}

                                </Form.Select>

                                <span>Диаметр крепежа</span>
                                <Form.Select className="mb-3" haria-label="Диаметр крепежа" filter-name="bolts_spacing" onChange={handleDiskSelectFilterChanged} value={disk.filter.bolts_spacing}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataDisk.bolts_spacing.map(el => {
                                        return <option value={el.val}>{el.text}</option>
                                    })}

                                </Form.Select>

                            </Col>
                            <Col lg={4}>
                                <span>Вылет</span>
                                <Form.Select className="mb-3" haria-label="Вылет" filter-name="et" onChange={handleDiskSelectFilterChanged} value={disk.filter.et}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataDisk.et.map(el => {
                                        return <option value={el.val}>{el.text}</option>
                                    })}

                                </Form.Select>

                                <span>Диаметр ступичного отверстия</span>
                                <Form.Select className="mb-3" haria-label="Диаметр ступичного отверстия" filter-name="dia" onChange={handleDiskSelectFilterChanged} value={disk.filter.dia}>
                                    <option value="" style={{ color: 'rgb(156, 156, 156)' }}>Не выбрано</option>
                                    {dataDisk.dia.map(el => {
                                        return <option value={el.val}>{el.text}</option>
                                    })}

                                </Form.Select>

                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                <Button variant="danger" size-md='lg' onClick={() => navigate(DISK_LIST_ROUTE)}>Показать</Button>
                            </Col>
                        </Row>
                    </Tab>

                </Tabs>
            </Container>

<div className="w-100 " style={{ paddingTop: '80px', paddingBottom: '80px', marginTop: '80px',background: '#e5e5e5'}}>
            <Container className="HomePage-popularSizes ">
                <Row><h3>Популярные размеры шин</h3></Row>
                <Row>
                    <Col xs={2} className="text-center">
                        <div className="font-weight-bold">R14</div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '175', height: '65', diameter: '14' })) }}>175/65 R14</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '185', height: '65', diameter: '14' })) }}>185/65 R14</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '185', height: '60', diameter: '14' })) }}>185/60 R14</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '185', height: '70', diameter: '14' })) }}>185/70 R14</Link></div>
                    </Col>
                    <Col xs={2}>
                        <div className="font-weight-bold">R15</div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '185', height: '65', diameter: '15' })) }}>185/65 R15</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '195', height: '65', diameter: '15' })) }}>195/65 R15</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '195', height: '60', diameter: '15' })) }}>195/60 R15</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '205', height: '70', diameter: '15' })) }}>205/70 R15</Link></div>
                    </Col>
                    <Col xs={2}>
                        <div className="font-weight-bold">R16</div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '205', height: '55', diameter: '16' })) }}>205/55 R16</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '215', height: '65', diameter: '16' })) }}>215/65 R16</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '215', height: '60', diameter: '16' })) }}>215/60 R16</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '205', height: '60', diameter: '16' })) }}>205/60 R16</Link></div>
                    </Col>
                    <Col xs={2}>
                        <div className="font-weight-bold">R17</div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '225', height: '65', diameter: '17' })) }}>225/65 R17</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '215', height: '55', diameter: '17' })) }}>215/55 R17</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '225', height: '50', diameter: '17' })) }}>225/50 R17</Link></div>
                        <div> <Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '235', height: '65', diameter: '17' })) }}>235/65 R17</Link></div>
                    </Col>
                    <Col xs={2}>
                        <div className="font-weight-bold">R18</div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '235', height: '60', diameter: '18' })) }}>235/60 R18</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '225', height: '55', diameter: '18' })) }}>225/55 R18</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '265', height: '60', diameter: '18' })) }}>265/60 R18</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '235', height: '55', diameter: '18' })) }}>235/55 R18</Link></div>
                    </Col>
                    <Col xs={2}>
                        <div className="font-weight-bold">R19</div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '265', height: '50', diameter: '19' })) }}>265/50 R19</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '275', height: '45', diameter: '19' })) }}>275/45 R19</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '285', height: '50', diameter: '19' })) }}>285/50 R19</Link></div>
                        <div><Link to={TIRES_LIST_ROUTE} onClick={() => { tires.setFilter(Object.assign(tires.filter, { width: '275', height: '40', diameter: '19' })) }}>275/40 R19</Link></div>
                    </Col>
                </Row>
            </Container>
</div>
        </div>
    )
})
export default HomePage