import { Col, Container, Image, Row } from "react-bootstrap"
import './style.css'
import snowflake from '../../assets/img/snowflake.png'
import studded from '../../assets/img/studded.png'
import sunny from '../../assets/img/sunny.png'
import { useContext, useEffect, useState } from "react"
import { declOfNum } from "../../utils/functions";
import noImage from '../../assets/img/wheel.png'
import { observer } from "mobx-react-lite"
import { Context } from "../.."

const CatalogListItem = observer(({ data, logistic, loading = false, hiddenSeason = true, onClick }) => {
    const { cart } = useContext(Context)


    const [inputValue, setInputValue] = useState(1)




    if (loading)
        return (<>


            <Col xl={3} lg={4} md={4} xs={6} className="p ssc-wrapper CatalogListItem-loading shadow-sm bg-white d-flex flex-column justify-content-center">
                <div className="w-100 CatalogListItem-item-photo" style={{}}></div>


                <div className="w-100 CatalogListItem-item-title d-flex justify-content-center text-center font-weight-bold p-2 px-4"><div className="ssc-head-line"></div></div>
                <div className="w-100 text-sm CatalogListItem-item-season d-flex justify-content-center     align-items-center p-0" ><div className="ssc-head-line mx-5" style={{ height: '14px' }}></div> </div>
                <div className="w-100 text-sm d-flex justify-content-center p-0"><div className="ssc-head-line mx-5" style={{ height: '15px', margin: '3px' }}></div> </div>
                <div className="w-100 text-sm d-flex justify-content-center p-0"><div className="ssc-head-line mx-5" style={{ height: '15px' }}></div> </div>
                <div className="w-100 CatalogListItem-item-price d-flex justify-content-center p-2">
                    <div className="font-weight-bold"> <div className="ssc-head-line mx-5" style={{ height: '30px', margin: '3px' }}> </div> </div>
                </div>
                <div className="w-100 CatalogListItem-item-price d-flex justify-content-evenly p-2">
                    <div className="input-group w-auto mb-3">
                        <div className="input-group-prepend">
                            <button onClick={() => { }} className="btn btn-secondary btn-sm h-100" style={{ borderTopRightRadius: 0, borderBottomRightRadius: 0 }} id="minus-btn"><i className="fa fa-minus"></i></button>
                        </div>
                        <input type="number" id="qty_input" className="form-control form-control-sm text-center" value="1" min="1" style={{ maxWidth: '50px' }} disabled />
                        <div className="input-group-prepend">
                            <button onClick={() => { }} className="btn btn-secondary btn-sm h-100" style={{ borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }} id="plus-btn"><i className="fa fa-plus"></i></button>
                        </div>
                    </div>
                    <div className="input-group w-auto mb-3">
                        <button className="btn btn-outline-danger btn-sm" disabled>В корзину</button>
                    </div>
                </div>
            </Col>
        </>
        )
    const whpr = (typeof data.whpr.wh_price_rest[0] !== 'undefined') ? data.whpr.wh_price_rest[0] : data.whpr.wh_price_rest

    let logistDays = 0


    logistic.forEach(el => {
        if (el.whId === whpr.wrh)
            logistDays = el.logistDays
    });

    let season = sunny
    if (data.season == 'w')
        season = studded
    if (data.thorn)
        season = snowflake

    let seasonText = `лето`
    if (data.season == 'w')
        seasonText = `зима`
    if (data.thorn)
        seasonText = `зима`




    const handlePlus = () => inputValue >= whpr.rest ? whpr.rest : setInputValue(inputValue + 1)
    const handleMinus = () => inputValue <= 1 ? 1 : setInputValue(inputValue - 1)




    return (


        <Col xl={3} lg={4} md={4} xs={6} className="p1-0  CatalogListItem shadow-sm bg-white d-flex flex-column justify-content-center cursor-pointer" onClick={() => onClick(data.code)}>
            <div className="w-100 CatalogListItem-item-photo" style={{ opacity: data.img_big_my === null ? '0.65' : 1, background: `url('${data.img_big_my === null || data.img_big_my.length < 10 ? noImage : data.img_big_my}')` }}></div>
            <div className="w-100 CatalogListItem-item-title d-flex justify-content-center text-center font-weight-bold p-2 px-4">{data.name}</div>
            <div className={`w-100 text-sm CatalogListItem-item-season d-flex justify-content-center     align-items-center p-0 ${hiddenSeason ? 'd-none' : ''}`}> {seasonText}&nbsp;&nbsp;<Image src={season} /> </div>
            <div className="w-100 text-sm d-flex justify-content-center p-0">под заказ ~ {logistDays + 1} {logistDays + 1 > 0 ? declOfNum(logistDays + 1, [' день', ' дня', ' дней']) : 'Уточняйте'}</div>
            <div className="w-100 text-sm d-flex justify-content-center p-0">Остаток: {whpr.rest > 4 ? '> 4' : whpr.rest}  шт.</div>
            <div className="w-100 CatalogListItem-item-price d-flex justify-content-center p-2">
                <div className="font-weight-bold">{whpr.price_rozn} руб.</div>
            </div>
            <div className="w-100 CatalogListItem-item-price d-flex flex-column flex-md-row align-items-center justify-content-evenly p-2">
                <div className="input-group w-auto mb-3">
                    <div className="input-group-prepend">
                        <button onClick={handleMinus} className="btn btn-secondary btn-sm h-100" style={{ borderTopRightRadius: 0, borderBottomRightRadius: 0 }} id="minus-btn"><i className="fa fa-minus"></i></button>
                    </div>
                    <input type="number" id="qty_input" className="form-control form-control-sm text-center" value={inputValue} min="1" max={whpr.rest} style={{ maxWidth: '50px' }} disabled />
                    <div className="input-group-prepend">
                        <button onClick={handlePlus} className="btn btn-secondary btn-sm h-100" style={{ borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }} id="plus-btn"><i className="fa fa-plus"></i></button>
                    </div>
                </div>
                <div className="input-group w-auto mb-3">
                    <button onClick={() => cart.addItem(data.code, inputValue)} className={`btn ${cart.checkItem(data.code) === false ? 'btn-outline-danger' : 'btn-outline-success'} btn-sm `}>{cart.checkItem(data.code) === false ? 'В корзину' : 'В корзине'}</button>
                </div>
            </div>
        </Col>

    )
})
export default CatalogListItem