import { makeAutoObservable } from "mobx"

export default class CartStore {
    constructor() {
        const ls = localStorage.getItem('cart')
        this._items = ls === null ? [] : JSON.parse( ls)
      
        makeAutoObservable(this)
    }

    addItem(item, count) {
        let inArray = false
        this._items.forEach(el => {
            if(el.code == item) inArray = true
        })
        if(!inArray)
            this._items.push({code: item, count: count}) 
        
        localStorage.setItem('cart', JSON.stringify(this._items) );
    }

    setItems(items) {
        this._items = items
        localStorage.setItem('cart', JSON.stringify(this._items) );
    }

    checkItem(code){
        let t = false
        this._items.forEach(el =>{
            if(el.code == code) t = el
        })
        if(t === false)
            return false
        return t
    }
    get count() {
        return this._items.length
    }

}