import { observer } from "mobx-react-lite"
import React, { useContext, useState, useEffect } from "react";
import { Context } from "../";
import { Col, Container, Form, Pagination, Row } from "react-bootstrap"
import CatalogListItem from "../components/CatalogListItem"
import { fetchTires } from "../http/catalog"
import { dataTires, dataWhr, dataWrh } from "../utils/data";
import { useNavigate } from "react-router-dom";
import TiresDetailPage from "./TiresDetailPage";
import { TIRES_LIST_ROUTE } from "../utils/consts";
import { declOfNum } from "../utils/functions";



const TiresListPage = observer(() => {
    const navigate = useNavigate()
    const { tires } = useContext(Context)

    const [isFetched, setIsFetched] = useState(false)
    const pageCount = Math.ceil(tires.totalCount / tires.limit)
    const pages = []
    
    for (let i = 0; i < pageCount; i++) {
        if (tires.page + 3 > i && tires.page - 3 < i)
            pages.push(i + 1)
    }

    const setPage = page => {
        window.scrollTo({ top: 0 });
        setIsFetched(true)
        tires.setItems([])
        fetchTires({ page: page, limit: tires.limit, filter: tires.filter }).then(data => {
            
            if (typeof data.GetFindTyreResult.price_rest_list.TyrePriceRest !== 'undefined') {
                if( Array.isArray(data.GetFindTyreResult.price_rest_list.TyrePriceRest))
                    tires.setItems(data.GetFindTyreResult.price_rest_list.TyrePriceRest)
                else
                    tires.setItems([data.GetFindTyreResult.price_rest_list.TyrePriceRest])
                tires.setLogistics(data.GetFindTyreResult.warehouseLogistics.WarehouseLogistic)
                tires.setTotalCount(data.GetFindTyreResult.totalPages * tires.limit)
            }
            setIsFetched(false)
        })
        tires.setPage(page)
    }


    useEffect(() => {
        window.scrollTo({ top: 0 });
        tires.setItems([])
        setIsFetched(true)
        
        fetchTires({ page: tires.page, limit: tires.limit, filter: tires.filter }).then(data => {
            
            if (typeof data.GetFindTyreResult.price_rest_list.TyrePriceRest !== 'undefined') {
                if( Array.isArray(data.GetFindTyreResult.price_rest_list.TyrePriceRest))
                    tires.setItems(data.GetFindTyreResult.price_rest_list.TyrePriceRest)
                else
                    tires.setItems([data.GetFindTyreResult.price_rest_list.TyrePriceRest])
                tires.setLogistics(data.GetFindTyreResult.warehouseLogistics.WarehouseLogistic)
                tires.setTotalCount(data.GetFindTyreResult.totalPages * tires.limit)
            }
            setIsFetched(false)
        })
    }, [tires.page, tires.filter.type_list])


    const handleSelectFilterChanged = (e) => {
        let temp = {}
        temp[e.target.attributes['filter-name'].value] = e.target.value
        tires.setFilter(Object.assign(tires.filter, temp))
        setPage(1)
    }

    return (
        <div>


            <Container className="mt-4 TiresListPage ">
                <Row>
                    <Col lg={2} >
                        <div class="">
                            <Row>
                        <div className="shadow-sm bg-white pe-lg-1">

                            <div className="px-2 py-2 font-weight-bold">
                            

                            <span>Наличие</span>
                            <Form.Select className="mb-3" haria-label="Наличие" filter-name="wrh" onChange={handleSelectFilterChanged} value={tires.filter.wrh}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataWrh.map(el => {
                                    return <option value={el.whId}>Под заказ {el.logistDays + 1} {el.logistDays + 1 > 0 ? declOfNum(el.logistDays + 1, [' день', ' дня', ' дней']) : 'Уточняйте'}</option>
                                })}
                         
                            </Form.Select>



                            <span>Сезон</span>
                            <Form.Select className="mb-3" aria-label="Сезон" filter-name="season" onChange={handleSelectFilterChanged} value={tires.filter.season}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataTires.season.map(el => {
                                    return <option value={el.val}>{el.text}</option>
                                })}
                         
                            </Form.Select>

                            <span>Производитель</span>
                            <Form.Select className="mb-3" haria-label="Производитель" filter-name="brand_list" onChange={handleSelectFilterChanged} value={tires.filter.brand_list}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataTires.brand_list.map(el => {
                                    return <option value={el.text}>{el.text}</option>
                                })}
                         
                            </Form.Select>

                            <span>Ширина</span>
                            <Form.Select className="mb-3" haria-label="Ширина" filter-name="width"  onChange={handleSelectFilterChanged} value={tires.filter.width}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataTires.width.map(el => {
                                    return <option value={el.text}>{el.text}</option>
                                })}
                         
                            </Form.Select>

                            <span>Высота</span>
                            <Form.Select className="mb-3" haria-label="Высота" filter-name="height"  onChange={handleSelectFilterChanged} value={tires.filter.height}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataTires.height.map(el => {
                                    return <option value={el.text}>{el.text}</option>
                                })}
                         
                            </Form.Select>

                            <span>Диаметр</span>
                            <Form.Select className="mb-3" haria-label="Диаметр" filter-name="diameter"  onChange={handleSelectFilterChanged} value={tires.filter.diameter}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataTires.diameter.map(el => {
                                    return <option value={el.text}>{el.text}</option>
                                })}
                         
                            </Form.Select>


                            <span>Тип шины</span>
                            <Form.Select className="mb-3" haria-label="Диаметр" filter-name="type_list"  onChange={handleSelectFilterChanged} value={tires.filter.type_list}>
                                <option value="" style={{color: 'rgb(156, 156, 156)'}}>Не выбрано</option>
                                {dataTires.type_list.map(el => {
                                    return <option value={el.val}>{el.text}</option>
                                })}
                         
                            </Form.Select>

                            </div>

                            
                        </div>
                        </Row>
                        </div>
                    </Col>
                    <Col lg={10} >
                        <div className="">
                            <Row>
                                {
                                    (!isFetched && tires.count === 0) ? 
                                        
                                            <>
                                                Ничего не нашлось
                                            </>
                                        : 
                                    
                                    (tires.count === 0 && isFetched) ?
                                        [...Array(20)].map((x, i) => {
                                            return <CatalogListItem key={i} loading />
                                        }
                                        )
                                        :
                                        tires.items.map(el => {
                                            return <CatalogListItem key={el.code} data={el} logistic={tires.logistics} hiddenSeason={false} onClick={code => navigate(TIRES_LIST_ROUTE +'/'+ code)}/>
                                        })
                                }





                            </Row>
                        </div>
                    </Col>
                </Row>
            </Container>


            {(pages.length > 1 && tires.count > 0) ? 
            <div className="w-100 d-flex justify-content-center" style={{ marginBottom: '60px', marginTop: '50px' }}>
                <Pagination className="mt-3" >
                    {pages.map(page =>
                        <Pagination.Item
                            key={page}
                            active={tires.page === page}
                            onClick={() => setPage(page)}
                        >
                            {page}
                        </Pagination.Item>
                    )}
                </Pagination>
            </div>
            : ''}

        </div>
    )
})
export default TiresListPage