import axios from "axios";
import {$host} from "./index";


export const fetchTires = async (param = {}) => {
    param.method = 'fetchTires'
    const data = await axios({
        method: 'post',
        url:  'https://shinatob.ru/api/index.php',
        data: param
      });
      
    /*const data = await $host.post('sendmail.php', param)*/
    return data.data
}
